.pika-meme > h6 {
  position: absolute;
}

.pika-meme > .text1 {
  top: 0px;
  width: 15.5rem;
}
.pika-meme > .text2 {
  top: 39px;
}

.pika-meme > .text3 {
  top: 60px;
}

.pika-meme > .text4 {
  top: 80px;
}

.suprisedPikaImg {
  height: 35.3vh;
  min-width: 15.5rem;
  position: relative;
  top: 0px;
}

.pika-meme {
  /* width: 67% */
  position: relative;
  left: -15px;
}

.meme-template {
  position: relative;
}

@media (max-width: 450px) {
  .pika-meme {
    left: 0px;
  }
}

@media (max-width: 400px) {
  .meme-template {
    transform: scale(0.9);
  }
}

@media (max-width: 350px) {
  .meme-template {
    transform: scale(0.8);
  }
}

@media (max-height: 650px) {
  .pika-meme > h6 {
    font-size: 15px;
  }
  .pika-meme > .text2 {
    top: 34px;
  }

  .pika-meme > .text3 {
    top: 54px;
  }

  .pika-meme > .text4 {
    top: 73px;
  }
}

@media (max-height: 600px) {
  .pika-meme > h6 {
    font-size: 13px;
  }
  .pika-meme > .text2 {
    top: 32px;
  }

  .pika-meme > .text3 {
    top: 50px;
  }

  .pika-meme > .text4 {
    top: 67px;
  }
}
