#hot-deals {
  transform: rotate(30deg);
  font-size: 40px;
}
.border {
  border-width: 3px;
}

.promo-text-div {
  position: relative;
  right: 25px;
}

@media (max-width: 460px) {
  .promo-div {
    transform: scale(0.8);
  }
}

@media (max-width: 360px) {
  .promo-div {
    transform: scale(0.7);
  }
}
