.letme-meme {
  position: relative;
  left: -15px;
  /* width:67% */
}

.letme-meme > h2 {
  position: absolute;
  color: rgb(49, 207, 83);
  text-align: center;
  font-family: impact, sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 16px;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.letmeImg {
  height: 35.3vh;
  min-width: 15.5rem;
  width: 19rem;
}
.letme-meme > .top1 {
  top: 25px;
  left: 10px;
  transform: rotate(-30deg);
  width: 100px;
}
.letme-meme > .bot1 {
  top: 30px;
  left: 150px;
  width: 100px;
  transform: rotate(30deg);
}

.letme-meme > .top2 {
  bottom: 30px;
  left: 10px;
  transform: rotate(-30deg);
  width: 100px;
}

.letme-meme > .bot2 {
  bottom: 15px;
  left: 150px;
  width: 100px;
  transform: rotate(30deg);
}

.product-card {
  height: 400px;
  width: 100%;
}

@media (max-width: 450px) {
  .letme-meme {
    left: 0px;
  }
}

@media (max-height: 650px) {
  .letme-meme > .top2 {
    bottom: 15px;
    left: 10px;
  }

  .letme-meme > .bot2 {
    bottom: 6px;
    left: 145px;
    width: 100px;
    transform: rotate(30deg);
  }
}

@media (max-height: 600px) {
  .letme-meme > h2 {
    font-size: 13px;
  }
  .letme-meme > .bot2 {
    bottom: 15px;
    left: 145px;
    width: 100px;
    transform: rotate(30deg);
  }
}
