.productListDiv {
  position: relative;
}
.memeTitle {
  position: absolute;
  text-align: center;
  font-size: 1rem;
  color: white;
  width: 35vh;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.list-img {
  height: 35vh;
  width: 35vh;
}

.border-width {
  width: 150px;
  height: 35.3vh;
  z-index: 2;
}

#priceFrame {
  position: relative;
  /* right: 30px; */
  left: -15px;
  height: 100%;
  min-width: 115px;
  max-height: 36.3vh;
}

#priceFrame > h4 {
  font-size: 16px;
}

#promo-text {
  position: relative;
  text-align: center;
  transform: rotate(25deg);
  font-size: 1.2rem;
}

.btn-position {
  position: relative;
  bottom: 5px;
}

#priceFrame .no-promo {
  font-size: 30px;
}

@media (max-width: 450px) {
  #priceFrame {
    left: 0px;
  }
}

@media (max-height: 680px) {
  .btn-position {
    padding: 3px 10px;
  }
  #priceFrame .price {
    font-size: 10px;
  }
  #promo-text {
    padding: 1px 10px !important;
  }
}
@media (max-height: 600px) {
  #promo-text {
    width: 60px;
    line-height: 1;
    top: 5px;
  }
}
