.kermit-meme {
  position: relative;
  left: -15px;
}

.kermit-meme > h6 {
  position: absolute;
  z-index: 3;
  width: 15.5rem;
  margin-left: 2px;
}

.kermit-meme > .text1 {
  top: 5px;
}

.kermit-meme > .text2 {
  top: 70px;
}
.product-card {
  height: 400px;
  width: 100%;
}

.kermitImg {
  height: 35.3vh;
  min-width: 15.5rem;
  position: relative;
  top: 0px;
}

@media (max-width: 450px) {
  .kermit-meme {
    left: 0px;
  }
}

@media (max-height: 650px) {
  .kermit-meme > h6 {
    font-size: 15px;
  }
  .kermit-meme > .text1 {
    top: 5px;
  }

  .kermit-meme > .text2 {
    top: 55px;
  }
}

@media (max-height: 600px) {
  .pika-meme > h6 {
    font-size: 13px;
  }
  .pika-meme > .text2 {
    top: 32px;
  }

  .pika-meme > .text3 {
    top: 50px;
  }

  .pika-meme > .text4 {
    top: 67px;
  }
}
