.firstWrld-meme {
  position: relative;
  left: -15px;
}
.firstWrld-meme > h4 {
  text-transform: uppercase;
  font-family: impact, sans-serif;
  letter-spacing: 0px;
  color: white;
  position: absolute;
  text-align: center;
  /* left: 60px; */
  min-width: 15.5rem;
  font-size: 19px;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.firstWrld-meme > .text1 {
  top: 0;
}
.firstWrld-meme > .text2 {
  bottom: 0px;
}

.product-card {
  height: 400px;
  width: 100%;
}

.firstWrldImg {
  height: 35.3vh;
  min-width: 15.3rem;
  max-width: 16rem;
}

@media (max-width: 450px) {
  .firstWrld-meme {
    left: 0px;
  }
}
