.mainPageListImg {
  height: 30vh;
  width: 25rem;
}
.mainPageListDiv {
  position: relative;
  display: inline-block;
  transition: 0.15s ease;
  z-index: 0;
}

.mainPageListDiv h2 {
  text-align: center;
  width: 100%;
  height: 0;
  font-size: 25px;
  line-height: 0;
  top: 20px;
  color: whitesmoke;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  position: absolute;
  z-index: 1;
}

.mainPageListDiv:hover {
  height: 31vh;
  width: 26rem;
  z-index: 10;
}
