.cartDiv {
  min-height: 100vh;
  height: 100%;
}
.cartImg {
  min-height: 120px;
  max-height: 175px;
  min-width: 120px;
  max-width: 175px;
}

.fa-plus-square {
  font-size: 8vh;
  cursor: pointer;
  transition: all 0.5s;
}
.fa-plus-square:hover {
  color: rgb(7, 153, 7);
}
.fa-minus-square {
  font-size: 8vh;
  cursor: pointer;
  transition: all 0.5s;
}
.fa-minus-square:hover {
  color: red;
}

.priceDiv {
  width: 300px;
}

#promo-text-cart {
  position: relative;
  left: 63%;
  top: 50px;
  transform: rotate(30deg);
  font-size: 2rem;
}

.summary-div {
  position: sticky;
  min-width: 300px;
  max-width: 310px;
  right: 80px;
  top: 100px;
}

.amountDiv {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}
.priceDiv {
  font-size: 23px;
}

@media (min-width: 991px) and (max-width: 1070px) {
  .amountDiv {
    margin-left: 0;
    margin-right: 0;
  }
  .priceDiv {
    font-size: 18px;
    width: 10rem;
  }
}

@media (max-width: 991px) {
  .summary-div {
    position: relative;
    right: 0;
    top: 0;
  }
}

@media (max-width: 420px) {
  .amountDiv {
    margin-left: 0;
    margin-right: 0;
  }
}

@media (max-width: 370px) {
  .summary-div {
    transform: scale(0.9);
  }
  .summary-div > div > div > h5 {
    font-size: 18px;
  }
  .priceDiv {
    width: 15rem;
  }
}

@media (max-width: 320px) {
  .priceDiv {
    font-size: 18px;
    width: 12rem;
  }
}

@media (min-height: 1000px) {
  .fa-plus-square {
    font-size: 70px;
  }
  .fa-minus-square {
    font-size: 70px;
  }
}
